import { e as error } from "../../../chunks/index.js";
import { s as serializeNonPOJOs } from "../../../chunks/setting.js";
import PocketBase from "pocketbase";
const pb = new PocketBase("https://hissing-machine.pockethost.io");
const load = () => {
  const getTopic = async () => {
    try {
      const topic = serializeNonPOJOs(await pb.collection("topic").getFullList(200, {
        sort: "-created	"
      }));
      return topic;
    } catch (err) {
      console.log("Error: ", err);
      throw error(err.status, err.message);
    }
  };
  const getGallery = async () => {
    try {
      const gallery = serializeNonPOJOs(await pb.collection("gallery").getFullList(200, {
        expand: "topicse,user"
      }));
      return gallery;
    } catch (err) {
      console.log("Error: ", err);
      throw error(err.status, err.message);
    }
  };
  return {
    topic: getTopic(),
    gallery: getGallery()
  };
};
export {
  load
};
