import { r as redirect, e as error } from "../../../chunks/index.js";
import { s as serializeNonPOJOs } from "../../../chunks/setting.js";
const load = ({ locals }) => {
  if (!locals.pb.authStore.isValid) {
    throw redirect(303, "/");
  }
  const getTopic = async () => {
    try {
      const topic = serializeNonPOJOs(await locals.pb.collection("topic").getFullList(200, {
        sort: "-created	"
      }));
      return topic;
    } catch (err) {
      console.log("Error: ", err);
      throw error(err.status, err.message);
    }
  };
  const getGallery = async () => {
    try {
      const gallery = serializeNonPOJOs(await locals.pb.collection("gallery").getFullList(200, {
        expand: "topicse,user"
      }));
      return gallery;
    } catch (err) {
      console.log("Error: ", err);
      throw error(err.status, err.message);
    }
  };
  return {
    topic: getTopic(),
    gallery: getGallery()
  };
};
const actions = {
  create: async ({ locals, request }) => {
    const formData = await request.formData();
    const submission = formData.get("submission");
    if (submission.length === 0) {
      return error("No submission provided");
    }
    formData.append("user", locals.user.id);
    try {
      await locals.pb.collection("gallery").create(formData);
    } catch (err) {
      console.log(err);
      throw error(err.status, err.message);
    }
    throw redirect(303, "/explore");
  }
};
export {
  actions,
  load
};
